import React, { useEffect, useState } from "react";
import { 
  MDBContainer, 
  MDBTable, 
  MDBTableHead, 
  MDBTableBody, 
  MDBBadge, 
  MDBSpinner
} from "mdb-react-ui-kit";

const API_URL = "https://script.google.com/macros/s/AKfycby7ANAR0E0geFDUp-Zi086Ie8KjFz7X5vcj1sQ4yIMg9yUDOPdd0LbyQYLqOs44aZxF/exec?action=racerlist&domain=ppvcup2024";

const RacerList = () => {
  const [racers, setRacers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchRacers = async () => {
      try {
        console.log("Načítám seznam závodníků...");
        const response = await fetch(API_URL);
        if (!response.ok) {
          throw new Error(`HTTP chyba! Status: ${response.status}`);
        }
        const data = await response.json();
        console.log("Odpověď serveru:", data);

        if (data.isError) {
          throw new Error("Chyba při načítání dat: " + data.message);
        }

        // Převod JSON stringu z `responseData` na objekt
        const parsedRacers = JSON.parse(data.responseData);
        setRacers(parsedRacers);
      } catch (err) {
        console.error("Chyba při načítání závodníků:", err);
        setError("Nepodařilo se načíst seznam závodníků.");
      } finally {
        setLoading(false);
      }
    };

    fetchRacers();
  }, []);

  if (loading) {
    return (
      <MDBContainer className="text-center my-4">
        <MDBSpinner role="status">
          <span className="visually-hidden">Načítám seznam závodníků...</span>
        </MDBSpinner>
      </MDBContainer>
    );
  }

  if (error) {
    return <p className="text-danger text-center my-4">{error}</p>;
  }

  return (
    <MDBContainer className="my-4">
      <h4 className="mb-4 text-center">Seznam registrovaných</h4>
      <MDBTable align="middle" striped bordered small>
        <MDBTableHead>
          <tr>
            <th>#</th>
            <th>Jméno</th>
            <th>Příjmení</th>
            <th>Aeroklub</th>
            <th>Typ letadla</th>
            <th>Imatrikulace</th>
            <th>Startovní znak</th>
            <th>Třída</th>
          </tr>
        </MDBTableHead>
        <MDBTableBody>
          {racers.map((racer, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{racer.name}</td>
              <td>{racer.surname}</td>
              <td>{racer.club}</td>
              <td>{racer.glider}</td>
              <td>{racer.imatriculation}</td>
              <td>{racer.startCode}</td>
              <td>
                <MDBBadge color={racer.gliderClass === "club" ? "success" : "primary"} pill>
                  {racer.gliderClass}
                </MDBBadge>
              </td>
            </tr>
          ))}
        </MDBTableBody>
      </MDBTable>
    </MDBContainer>
  );
};

export default RacerList;
